.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

p.info {
  font-size: calc(4px + 2vmin);
  margin: calc(1px + 1vmin);
}

h4.info {
  margin: calc(15px + 1vmin);
  margin-bottom: 1vmin;
}

/* Divider */
.divider-container{
  display: flex;
  align-items: center;
  margin-bottom: calc(15px + 1vmin);
}

.divider-border{
  border-bottom: 1px solid rgba(255, 255, 255, 0.479);
  width: 100%;
}
